import {Component, Input, OnInit} from '@angular/core';

@Component({
  selector: 'app-json-selector',
  templateUrl: './json-selector.component.html',
  styleUrls: ['./json-selector.component.scss']
})
export class JsonSelectorComponent implements OnInit {
  @Input() json: any;
  @Input() deep: number;
  @Input() jsonPath: string;
  @Input() isInArray: boolean;
  constructor() { }

  colors = ['23,147,135', '80,96,187', '53,159,244', '84,168,87', '232,186,54'];

  ngOnInit(): void {
    console.log(this.deep)
    this.deep = this.deep + 1;
  }

  isItemObject(variable: any) {
    return (typeof variable === "object" || typeof variable === 'function') && (variable !== null) && !Array.isArray(variable);
  }

  isItemArray(variable: any){
    return Array.isArray(variable)
  }

  getColor(deep: number){
    if(deep > 3){
      deep = 0;
    }
    return 'rgb(' + this.colors[deep] + ')';
  }

  getObjectPropertiesNumber(json: any){
    return Object.keys(json).length;
  }

  logJsonPath(jsonPath){
    console.log(jsonPath);
  }
}
